export enum ValidateType {
  Login = "login",
  Password = "password",
  NewPassword = "newPassword",
  OldPassword = "oldPassword",
  CheckPassword = "check_password",
  FirstName = "first_name",
  SecondName = "second_name",
  Email = "email",
  Phone = "phone",
  DisplayName = "display_name",
  Message = "message",
}

type ValidateRule = {
  type: ValidateType | string
  value: string
}

export function validateForm(rules: ValidateRule[]) {
  let errorMessage = { text: "", inputName: "" }
  for (let i = 0; i < rules.length; i++) {
    const { type, value } = rules[i]
    //     " " ,      onBlur    type:inputEl.name,      
    if (type === ValidateType.Login) {
      errorMessage.inputName = "loginInputRef"
      if (value.length === 0) {
        errorMessage.text = "    "
        break
      } else if (value.length < 3) {
        errorMessage.text = "    3 "
        break
      } else if (value.length > 20) {
        errorMessage.text = "    20 "
        break
      } else if (!value.match(/[a-zA-Z-]+/g)) {
        errorMessage.text = "     "
        break
      } else if (value.match(/\s/g)) {
        errorMessage.text = "    "
        break
      } else if (value.match(/[-]/g)) {
        errorMessage.text = "     "
        break
      } else if (!value.match(/^[a-zA-Z0-9-_]{3,20}$/g)) {
        errorMessage.text = "     "
        break
      }
    }
    if (
      type === ValidateType.Password ||
      type === ValidateType.NewPassword ||
      type === ValidateType.OldPassword ||
      type === ValidateType.CheckPassword
    ) {
      errorMessage.inputName = `${type}InputRef`
      if (value.length === 0) {
        errorMessage.text = "    "
        break
      } else if (value.length < 7) {
        errorMessage.text = "    8 "
        break
      } else if (value.length > 40) {
        errorMessage.text = "    40 "
        break
      } else if (!value.match(/\d+/g)) {
        errorMessage.text = "      "
        break
      } else if (!value.match(/[A-Z-]+/g)) {
        errorMessage.text = "      "
        break
      }
    }
    if (type === ValidateType.FirstName || type === ValidateType.SecondName) {
      errorMessage.inputName = `${type}InputRef`
      const nameForText = type === ValidateType.FirstName ? "" : ""
      if (value.length === 0) {
        errorMessage.text = `${nameForText}    ${nameForText === "" ? "" : ""}`
        break
      } else if (!value.match(/^[A-Z-]/g)) {
        errorMessage.text = "    "
        break
      } else if (value.match(/\d+/g)) {
        errorMessage.text = ",  "
        break
      } else if (value.match(/\s/g)) {
        errorMessage.text = `${nameForText}    `
        break
      } else if (!value.match(/^[a-zA-Z--0-9-]{0,}$/g)) {
        errorMessage.text = `${nameForText}     `
        break
      }
    }
    if (type === ValidateType.Email) {
      errorMessage.inputName = "emailInputRef"
      if (value.length === 0) {
        errorMessage.text = "    "
        break
      } else if (value.match(/\s/g)) {
        errorMessage.text = "    "
        break
      } else if (value.match(/[-]/g)) {
        errorMessage.text = "     "
        break
      } else if (!value.match(/^[a-zA-Z0-9-_@.]{0,}$/g)) {
        errorMessage.text = "     "
        break
      } else if (!value.match(/[@]/g)) {
        errorMessage.text = "     @"
        break
      } else if (!value.match(/[.]/g)) {
        errorMessage.text = "    "
        break
      } else if (value.match(/[.]/g) && !value.match(/\w+[.]\w+/g)) {
        errorMessage.text = "        "
        break
      }
    }
    if (type === ValidateType.Phone) {
      errorMessage.inputName = "phoneInputRef"
      if (value.length === 0) {
        errorMessage.text = "     "
        break
      } else if (value.match(/\s/g)) {
        errorMessage.text = "     "
        break
      } else if (value.match(/[A-Za-z--]+/g)) {
        errorMessage.text = "     "
        break
      } else if (!value.match(/^[+]7/)) {
        errorMessage.text = "     +7"
        break
      } else if (value.length < 10) {
        errorMessage.text = "     10 "
        break
      } else if (value.length > 15) {
        errorMessage.text = "     15 "
        break
      }
    }
    if (type === ValidateType.DisplayName) {
      errorMessage.inputName = "display_nameInputRef"
      if (value.length === 0) {
        errorMessage.text = "      "
        break
      }
    }
    if (type === ValidateType.Message) {
      errorMessage.inputName = "messageInputRef"
      if (value.length === 0) {
        errorMessage.text = "    "
        break
      }
    }
  }

  return errorMessage
}
